/* css transition */
.main_app-fade-enter{
  visibility: visible;
  opacity: 0;
  -webkit-transform:scale(0.9);
          transform:scale(0.9);
}
.main_app-fade-enter-active{
  opacity: 1;
  -webkit-transform:scale(1);
          transform:scale(1);
  -webkit-transition:all 0.3s 1s ease-in-out;
  transition:all 0.3s 1s ease-in-out;
}
.main_app-fade-enter-done{
  visibility: visible;
}
.main_app-fade-exit-active{
  opacity:0;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  -webkit-transition:all .3s ease-in-out;
  transition:all .3s ease-in-out;
}
.main_app-fade-exit-done{
  opacity: 0;
  
}
